body {
	background-color: blue;
	heigth: 100%;
	width: 100%;
}

.calculator {
	width: 400px;
	height: 450px;
	background-color: white;
	padding: 5px;
	text-align: center;
	margin: auto;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}



.buttons {

	width: 250px;
	margin: 10px auto;
}

.button {
	float: left;
	width: 57.5px;
	height: 50px;
	margin: 2px;
}

.textbox {
	margin: 5px;
	border: 1px solid #000;
	height: 25px;
	margin: 0 auto;
	text-align: right;
	width: 270px;
}